import { makeStyles } from '@material-ui/core/styles';
import { Card, CardMedia, CardContent, Typography, Chip } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    card: {
        maxWidth: 345,
        margin: 'auto',
        borderRadius: '12px',
        boxShadow: '0 4px 20px rgba(0,0,0,0.08)',
        transition: 'transform 0.3s ease, box-shadow 0.3s ease',
        '&:hover': {
            transform: 'translateY(-5px)',
            boxShadow: '0 8px 25px rgba(0,0,0,0.12)',
        },
    },
    media: {
        height: 200,
        borderTopLeftRadius: '12px',
        borderTopRightRadius: '12px',
    },
    content: {
        padding: theme.spacing(2),
        textAlign: 'left',
        backgroundColor: '#ffffff', // White background for card content
        color: '#333333', // Dark text
    },
    categoryChip: {
        backgroundColor: '#3A72A1', // Primary blue
        color: '#ffffff',
        marginBottom: theme.spacing(1),
        fontSize: '0.75rem',
        height: '24px',
    },
    title: {
        fontWeight: 'bold',
        fontSize: '1.25rem',
        marginBottom: theme.spacing(1),
        color: '#2c3e50', // Darker blue/gray for title
    },
    description: {
        fontSize: '0.9rem',
        color: '#555555', // Medium gray for description
        lineHeight: 1.6,
    },
}));

const GuideCard = ({ image, category, title, description }) => {
    const classes = useStyles();

    return (
        <Card className={classes.card}>
            <CardMedia
                className={classes.media}
                image={image}
                title={title}
            />
            <CardContent className={classes.content}>
                {category && <Chip label={category} className={classes.categoryChip} />}
                <Typography gutterBottom variant="h5" component="h2" className={classes.title}>
                    {title}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p" className={classes.description}>
                    {description}
                </Typography>
            </CardContent>
        </Card>
    );
};

export default GuideCard;