import { makeStyles } from '@material-ui/core/styles';
import { Card, CardMedia, CardContent, Typography, Chip } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    card: {
        maxWidth: 360, // Slightly wider for blog posts
        margin: 'auto',
        borderRadius: '12px',
        boxShadow: '0 4px 20px rgba(0,0,0,0.08)',
        transition: 'transform 0.3s ease, box-shadow 0.3s ease',
        backgroundColor: '#ffffff', // White background for card
        color: '#333333', // Dark text
        display: 'flex',
        flexDirection: 'column',
        height: '100%', // Ensure cards in a row have same height if needed
        '&:hover': {
            transform: 'translateY(-5px)',
            boxShadow: '0 8px 25px rgba(0,0,0,0.12)',
        },
    },
    media: {
        height: 220, // Taller image for blog posts
        borderTopLeftRadius: '12px',
        borderTopRightRadius: '12px',
    },
    content: {
        padding: theme.spacing(2.5), // Slightly more padding
        textAlign: 'left',
        flexGrow: 1,
    },
    categoryChip: {
        backgroundColor: '#F58634', // Accent orange for blog category
        color: '#ffffff',
        marginBottom: theme.spacing(1),
        fontSize: '0.75rem',
        height: '24px',
    },
    title: {
        fontWeight: 'bold',
        fontSize: '1.3rem', // Larger title for blog posts
        marginBottom: theme.spacing(1),
        color: '#3A72A1', // Primary blue for title
        lineHeight: 1.3,
    },
    meta: {
        fontSize: '0.8rem',
        color: '#757575', // Lighter gray for meta info
        marginBottom: theme.spacing(1.5),
    },
    excerpt: {
        fontSize: '0.95rem',
        color: '#424242', // Darker gray for excerpt
        lineHeight: 1.6,
    },
}));

const PostCard = ({ image, category, title, date, author, excerpt }) => {
    const classes = useStyles();

    return (
        <Card className={classes.card}>
            <CardMedia className={classes.media} image={image} title={title} />
            <CardContent className={classes.content}>
                {category && <Chip label={category} className={classes.categoryChip} />}
                <Typography variant="h6" component="h3" className={classes.title}>
                    {title}
                </Typography>
                <Typography variant="caption" display="block" className={classes.meta}>
                    {date} {author && `| By ${author}`}
                </Typography>
                <Typography variant="body2" className={classes.excerpt}>
                    {excerpt}
                </Typography>
            </CardContent>
        </Card>
    );
};

export default PostCard;