import { makeStyles } from '@material-ui/core/styles';
import { Box, Typography, Grid, Container } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(8, 0),
        backgroundColor: '#ffffff', // White background
    },
    sectionTitle: {
        textAlign: 'center',
        marginBottom: theme.spacing(6),
        fontWeight: 'bold',
        color: '#3A72A1', // Primary blue
    },
    imagePlaceholder: {
        width: '100%',
        paddingBottom: '100%', // Creates a square aspect ratio
        backgroundColor: '#e0e0e0',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        borderRadius: '8px',
        color: '#757575',
    },
}));

const instagramPlaceholders = Array(6).fill(null); // Create 6 placeholders

const InstagramFeed = () => {
    const classes = useStyles();

    return (
        <Box className={classes.root}>
            <Container maxWidth="lg">
                <Typography variant="h4" component="h2" className={classes.sectionTitle}>
                    Follow Us On Instagram
                </Typography>
                <Grid container spacing={2}>
                    {instagramPlaceholders.map((_, index) => (
                        <Grid item xs={6} sm={4} md={2} key={index}>
                            <Box className={classes.imagePlaceholder}>
                                <Typography variant="caption">Insta Post {index + 1}</Typography>
                            </Box>
                        </Grid>
                    ))}
                </Grid>
            </Container>
        </Box>
    );
};

export default InstagramFeed;